@import "../../styles/index.scss";
.App {
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 50px;
    height: 60px;
    box-shadow: 0 2px 8px $gray-200;
    .header-left {
      display: flex;
      align-items: center;
      .logo {
        width: 40px;
        height: 40px;
      }
      .title {
        padding-left: 20px;
        font-size: $font-size-lg;
        font-weight: $font-weight-bold;
      }
    }
    .header-right {
      .githubSvg {
        width: 25px;
        height: 25px;
      }
    }
  }
  .container {
    display: flex;
    .aside {
      padding-top: 50px;
      width: 300px;
      height: calc(100vh - 60px);
      overflow-x: hidden;
      overflow-y: auto;
      &::-webkit-scrollbar {
        display: none;
      }
      &:hover {
        &::-webkit-scrollbar {
          display: block;
        }
      }
      .pa-menu {
        height: 100%;
        .menu-item {
          position: relative;
          a {
            display: inline-block;
            width: 100%;
            height: 100%;
            color: inherit;
            text-decoration: none;
            &::after {
              content: "";
              display: block;
              height: 100%;
              width: 100%;
              position: absolute;
              left: 0;
              top: 0;
            }
          }
        }
      }
    }
    .main {
      padding: 50px 100px;
      width: 100%;
      height: calc(100vh - 60px);
      overflow-x: hidden;
      overflow-y: auto;
    }
  }
}
